.formLogin{
    display: flex;
    flex-direction: column;
    height: 250px;
    width: 400px;
    padding: 70px 50px;
    justify-content: space-between;
    border: 1px solid #0005;
    border-radius: 15px;
    box-shadow: 1px 1px 5px #0003;
    background-color: #fff;
}

@media screen and (max-width:900px){
    .formLogin{
        width: 250px;
    }
}

@media screen and (max-width:500px){
    .formLogin{
        width: 90%;
        box-shadow: none;
        border: none;
        
    }
}